import React, { useState } from 'react';
import axios from 'axios';
import { useHistory } from 'react-router-dom';
import { ButtonGroup, ToggleButton } from 'react-bootstrap';
import '../App.css';
import createUtilityClassName from 'react-bootstrap/esm/createUtilityClasses';

function AddReview() {

    const [pageTitle, setPageTitle] = useState(localStorage.cityReview ? 'Add Review' : 'Add City');
    const [checked, setChecked] = useState(false);
    const [cityName, setCityName] = useState(localStorage.cityReview);
    const [stateName, setStateName] = useState(localStorage.stateReview);
    const [review, setReview] = useState('');
    const [ratingEnt, setRatingEnt] = useState('0');
    const [ratingNat, setRatingNat] = useState('0');
    const [ratingCos, setRatingCos] = useState('0');
    const [ratingSaf, setRatingSaf] = useState('0');
    const [ratingCul, setRatingCul] = useState('0');
    const [ratingTra, setRatingTra] = useState('0');
    const [ratingFoo, setRatingFoo] = useState('0');

    const radios = [
        { name: '1', value: '1' },
        { name: '2', value: '2' },
        { name: '3', value: '3' },
        { name: '4', value: '4' },
        { name: '5', value: '5' }
    ];

    const history = useHistory();
    const [message, setMessage] = useState('');

    const addReview = async event => {
        event.preventDefault();

        // // Add City
        // var obj = { 
        //     name:cityName,
        //     state:stateName,
        //     country:'',
        //     jwtToken:localStorage.accessToken};
        // var js = JSON.stringify(obj);

        // var config = {
        //     method: 'post',
        //     url: 'https://rate-my-city.herokuapp.com/api/createCity',
        //     // url: 'https://jsonplaceholder.typicode.com/posts',
        //     headers: { 'Content-Type': 'application/json' },
        //     data: js
        // };

        // axios(config)
        //     .then(function (response) {
        //         var res = response.data;
        //         if (res.error) {
        //             setMessage(res.error);
        //         } else {
        //             setMessage('');
        //             localStorage.setItem("accessToken", res.jwtToken);
        //         }
        //     })


        // Add Review
        var obj = { 
            email:localStorage.userEmail,
            city:cityName,
            state:stateName,
            rating:{
                entertainment:ratingEnt,
                nature:ratingNat,
                cost:ratingCos,
                safety:ratingSaf,
                culture:ratingCul,
                transportation:ratingTra,
                food:ratingFoo},
            review:review,
            jwtToken:localStorage.accessToken};
        var js = JSON.stringify(obj);

        var config = {
            method: 'post',
            url: 'https://rate-my-city.herokuapp.com/api/addRating',
            // url: 'https://jsonplaceholder.typicode.com/posts',
            headers: { 'Content-Type': 'application/json' },
            data: js
        };

        axios(config)
            .then(function (response) {
                var res = response.data;
                if (res.error) {
                    setMessage(res.error);
                } else {
                    setMessage('');
                    returnFromAddCity(res);
                }
            })
    }

    function returnFromAddCity(result) {
        localStorage.setItem('accessToken', result.jwtToken.accessToken);
        localStorage.setItem('cityReview', '')
        localStorage.setItem('stateReview', '')
        history.push("/");
    }

    function handleClickCancel() {
        localStorage.setItem('cityReview', '')
        localStorage.setItem('stateReview', '')
        history.push("/");
    }


    return(
        <div className="container-fluid h-custom mt-0">
            <div className="row d-flex justify-content-center align-items-center h-100">
                <div className="col-md-8 col-lg-6 col-xl-4 offset-xl-1">
                    <form >
                        <div style={{display: 'flex', justifyContent: 'center', alignItems: 'center', height: '8vh'}}>
                            <h1>{pageTitle}</h1>
                        </div>

                        <div className="divider d-flex align-items-center mt-0 my-4">
                            <p className="text-center fw-bold mx-3 mb-3"></p>
                        </div>
                        <div className="form-outline mt-1 mb-2">
                            {localStorage.cityReview ? 
                                <input type="text" autoFocus 
                                    className="form-control form-control-lg" 
                                    value={cityName}
                                    onChange={(e) => {setCityName(e.target.value)}}
                                    placeholder="Only enter US city" 
                                    readOnly
                                />
                                :
                                <input type="text" autoFocus 
                                    className="form-control form-control-lg" 
                                    value={cityName}
                                    onChange={(e) => {setCityName(e.target.value)}}
                                    placeholder="Only enter US city" 
                                />
                            }
                            <label className="form-label">Enter City</label>
                        </div>

                        <div className="form-outline mb-2">
                            {localStorage.cityReview ? 
                                <input type="text" 
                                    className="form-control form-control-lg" 
                                    value={stateName}
                                    onChange={(e) => {setStateName(e.target.value)}}
                                    placeholder="Only enter US state" 
                                    readOnly
                                />
                                :
                                <input type="text" 
                                    className="form-control form-control-lg" 
                                    value={stateName}
                                    onChange={(e) => {setStateName(e.target.value)}}
                                    placeholder="Only enter US state" 
                                />
                            }
                            <label className="form-label">Enter State</label>
                        </div>

                        <div className="form-outline mb-3">
                            <input type="text" 
                                className="form-control form-control-lg" 
                                value={review}
                                onChange={(e) => {setReview(e.target.value)}}
                                placeholder="Enter your review" 
                            />
                            <label className="form-label">Enter Comment.  (Max. 250 characters)</label>
                        </div>

                        <div className="div-table">
                            <table className="inline-table">
                                <tr>
                                    <td>Entertainment:&nbsp;</td>
                                    <td>
                                        <ButtonGroup>
                                            {radios.map((radio, idx) => (
                                            <ToggleButton
                                                key={idx}
                                                id={`radio1-${idx}`}
                                                type="radio"
                                                variant={'outline-secondary secondary text-dark '}
                                                name="radio1"
                                                value={radio.value}
                                                checked={ratingEnt === radio.value}
                                                onChange={(e) => setRatingEnt(e.currentTarget.value)}
                                            >
                                                {radio.name}
                                            </ToggleButton>
                                            ))}
                                        </ButtonGroup>
                                    </td>

                                </tr>

                                <tr>
                                    <td>Nature :</td>
                                    <td>
                                        <ButtonGroup>
                                            {radios.map((radio, idx) => (
                                            <ToggleButton
                                                key={idx}
                                                id={`radio2-${idx}`}
                                                type="radio"
                                                variant={'outline-secondary secondary text-dark'}
                                                name="radio2"
                                                value={radio.value}
                                                checked={ratingNat === radio.value}
                                                onChange={(e) => setRatingNat(e.currentTarget.value)}
                                            >
                                                {radio.name}
                                            </ToggleButton>
                                            ))}
                                        </ButtonGroup>
                                    </td>

                                </tr>

                                <tr>
                                    <td>Cost :</td>
                                    <td>
                                        <ButtonGroup>
                                            {radios.map((radio, idx) => (
                                            <ToggleButton
                                                key={idx}
                                                id={`radio3-${idx}`}
                                                type="radio"
                                                variant={'outline-secondary secondary text-dark'}
                                                name="radio3"
                                                value={radio.value}
                                                checked={ratingCos === radio.value}
                                                onChange={(e) => setRatingCos(e.currentTarget.value)}
                                            >
                                                {radio.name}
                                            </ToggleButton>
                                            ))}
                                        </ButtonGroup>
                                    </td>

                                </tr>

                                <tr>
                                    <td>Safety :</td>
                                    <td>
                                        <ButtonGroup>
                                            {radios.map((radio, idx) => (
                                            <ToggleButton
                                                key={idx}
                                                id={`radio4-${idx}`}
                                                type="radio"
                                                variant={'outline-secondary secondary text-dark'}
                                                name="radio4"
                                                value={radio.value}
                                                checked={ratingSaf === radio.value}
                                                onChange={(e) => setRatingSaf(e.currentTarget.value)}>
                                                {radio.name}
                                            </ToggleButton>
                                            ))}
                                        </ButtonGroup>
                                    </td>
                                </tr>

                                <tr>
                                    <td>Culture :</td>
                                    <td>
                                        <ButtonGroup>
                                            {radios.map((radio, idx) => (
                                            <ToggleButton
                                                key={idx}
                                                id={`radio5-${idx}`}
                                                type="radio"
                                                variant={'outline-secondary secondary text-dark'}
                                                name="radio5"
                                                value={radio.value}
                                                checked={ratingCul === radio.value}
                                                onChange={(e) => setRatingCul(e.currentTarget.value)}>
                                                {radio.name}
                                            </ToggleButton>
                                            ))}
                                        </ButtonGroup>
                                    </td>
                                </tr>

                                <tr>
                                    <td>Transportation:&nbsp;&nbsp;&nbsp; </td>
                                    <td>
                                        <ButtonGroup>
                                            {radios.map((radio, idx) => (
                                            <ToggleButton
                                                key={idx}
                                                id={`radio6-${idx}`}
                                                type="radio"
                                                variant={'outline-secondary secondary text-dark'}
                                                name="radio6"
                                                value={radio.value}
                                                checked={ratingTra === radio.value}
                                                onChange={(e) => setRatingTra(e.currentTarget.value)}>
                                                {radio.name}
                                            </ToggleButton>
                                            ))}
                                        </ButtonGroup>
                                    </td>
                                </tr>

                                <tr>
                                    <td>Food: </td>
                                    <td>
                                        <ButtonGroup>
                                            {radios.map((radio, idx) => (
                                            <ToggleButton
                                                key={idx}
                                                id={`radio7-${idx}`}
                                                type="radio"
                                                variant={'outline-secondary secondary text-dark'}
                                                name="radio7"
                                                value={radio.value}
                                                checked={ratingFoo === radio.value}
                                                onChange={(e) => setRatingFoo(e.currentTarget.value)}>
                                                {radio.name}
                                            </ToggleButton>
                                            ))}
                                        </ButtonGroup>
                                    </td>
                                </tr>
                            </table>
                        </div>

                        <div className="form-group row d-flex justify-content-center mt-4">
                            <div className="col-md-3">
                                <button type="button" className="btn btn-secondary btn-lg align-items-center" onClick={addReview}>Add</button>
                            </div>

                            <div className="col-md-3">
                                <button type="button" className="btn btn-dark btn-lg align-items-center" onClick={handleClickCancel}>Cancel</button>
                            </div>

                            <div className="divider d-flex align-items-center mt-1 my-4">
                                <p className="text-center fw-bold mx-3 mb-5">{message}</p>
                            </div>
                        </div>

                    </form>
                </div>
            </div>
        </div>

    );
};

export default AddReview;