
import React, { Component, useState, useEffect } from 'react';
import { Table, Dropdown, Button, Modal } from 'react-bootstrap';
import { withRouter } from 'react-router-dom';
import axios from 'axios';
import "bootstrap/dist/css/bootstrap.min.css";
import '../App.css';
import 'react-bootstrap-typeahead/css/Typeahead.css';
import paginationFactory from 'react-bootstrap-table2-paginator';
// import reviews from "./data.js"
import dateFormat from "moment";
import thumbsup from '../thumbs-up.png';



class ShowReviews extends Component {

    

    constructor(props) {
        super(props)
        this.state = {
            reviews:[],
            message:'',
            showOptions:localStorage.accessToken ? true : false
        }
        this.routerChange = this.routerChange.bind(this);
        this.deleteReview = this.deleteReview.bind(this);

    }
    

    routerChange(cty, ste) {
        localStorage.setItem("cityReview", cty);
        localStorage.setItem("stateReview", ste);
        this.props.history.push("/AddReview");
    };

    deleteReview(cty, ste) {

        if ( window.confirm('Are you sure you want to delete this review?')) {
            this.obj = { 
                email:localStorage.userEmail,
                city:cty,
                jwtToken:localStorage.accessToken};
            this.js = JSON.stringify(this.obj);
    
            this.config = {
                method: 'post',
                url: 'https://rate-my-city.herokuapp.com/api/delete',
                headers: { 'Content-Type': 'application/json' },
                data: this.js
            };
    
            axios(this.config)
                .then(response => {
                    var res = response.data;
                    if (res.error) {
                        this.setState({
                            message: res.error
                        });
                    } else {
                        this.setState({
                            message: ''
                        });
                        localStorage.accessToken = res.jwtToken.accessToken;
                        window.location.reload(false);

                    }
                })
        } 
    }
    
    componentDidMount() {
        this.axiosUrl = 'https://rate-my-city.herokuapp.com/api/';

        if ( !localStorage.userNameSelected) {
            this.axiosUrl += 'searchCityState';
        } else {
            this.axiosUrl += 'searchUsername';
        }

        this.obj = { 
            city:localStorage.citySelected,
            state:localStorage.stateSelected,
            userName:localStorage.userNameSelected};
        this.js = JSON.stringify(this.obj);

        this.config = {
            method: 'post',
            url: this.axiosUrl,
            headers: { 'Content-Type': 'application/json' },
            data: this.js
        };

        axios(this.config)
            .then(response => {
                var res = response.data;
                if (res.error) {
                    this.setState({
                        message: res.error
                    });
                } else {
                    this.setState({
                        message: '',
                        reviews: res
                    });
                }
            })
    }

    render () {
        const {reviews} = this.state;
        const {showOptions} = this.state;
        return (
            <body>
                <div className="container input-group justify-content-center">
                    <table className="master-table">
                    {
                        reviews.map((rec => 
                            <>

                            <tr className="city-separator">
                                <td>
                                    <span>
                                        {rec.name}, &nbsp;&nbsp; {rec.state}
                                    </span>
                                </td>
                                {showOptions && 
                                    <td>
                                        <span>
                                            <button className="btn btn-sm btn-secondary" onClick={() => this.routerChange(rec.name, rec.state)} type="button">Add Review</button>
                                        </span>
                                    </td>
                                }
                            </tr>
                            <div className="row justify-content-center">
                                <div className="col-auto">
                                    <table className="table table-responsive mb-1 mt-0">
                                        <tr className="rating-avg">
                                            <td>Cost:</td>
                                            <td>{parseFloat(rec.averageRating.cost).toFixed(1)}</td>
                                            <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
                                            <td>Nature:</td>
                                            <td>{parseFloat(rec.averageRating.nature).toFixed(1)}</td>
                                            <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
                                            <td>Culture:</td>
                                            <td>{parseFloat(rec.averageRating.culture).toFixed(1)}</td>
                                            <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
                                            <td>Safety:</td>
                                            <td>{parseFloat(rec.averageRating.safety).toFixed(1)}</td>
                                            <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
                                            <td>Entertaiment:</td>
                                            <td>{parseFloat(rec.averageRating.entertainment).toFixed(1)}</td>
                                            <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
                                            <td>Transportation:</td>
                                            <td>{parseFloat(rec.averageRating.transportation).toFixed(1)}</td>
                                            <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
                                            <td>Food:</td>
                                            <td>{parseFloat(rec.averageRating.food).toFixed(1)}</td>
                                        </tr>
                                    </table>
                                </div>
                            </div>

                            <div>
                                {rec.ratings.map(function (rate, i) {
                                    return <form key={i} className="box">
                                        <div>
                                            <div className="line-delete">
                                                <span className="user">
                                                    {rate.userdetails.userName} wrote in {dateFormat(rate.time).format('MM-DD-YYYY @ hh:mm A')}: 
                                                    
                                                </span>
                                                {rate.userdetails.userName === localStorage.userName &&
                                                    <div>

                                                        <span className="action">
                                                            <button className="delete-button" onClick={() => this.deleteReview(rec.name, rec.state)} type="button">Delete</button>
                                                        </span>
                                                    </div>
                                                }
                                                <div>
                                                    <div><p>&nbsp;</p></div>
                                                    <p className="rate-comment">
                                                        {rate.description} &nbsp;&nbsp;&nbsp; <img src={thumbsup}/>
                                                    </p>
                                                </div>
                                            </div>
                                            
                                        </div>
                                        <div className="row justify-content-center">
                                            <div className="col-auto">
                                                <table className="table table-responsive mb-1 mt-0">
                                                    <tr className="rating-avg">
                                                        <td>Cost:</td>
                                                        <td>{parseFloat(rate.rating.cost).toFixed(1)}</td>
                                                        <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
                                                        <td>Nature:</td>
                                                        <td>{parseFloat(rate.rating.nature).toFixed(1)}</td>
                                                        <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
                                                        <td>Culture:</td>
                                                        <td>{parseFloat(rate.rating.culture).toFixed(1)}</td>
                                                        <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
                                                        <td>Safety:</td>
                                                        <td>{parseFloat(rate.rating.safety).toFixed(1)}</td>
                                                        <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
                                                        <td>Entertaiment:</td>
                                                        <td>{parseFloat(rate.rating.entertainment).toFixed(1)}</td>
                                                        <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
                                                        <td>Transportation:</td>
                                                        <td>{parseFloat(rate.rating.transportation).toFixed(1)}</td>
                                                        <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
                                                        <td>Food:</td>
                                                        <td>{parseFloat(rate.rating.food).toFixed(1)}</td>
                                                    </tr>
                                                </table>
                                            </div>
                                        </div>
                                    </form>
                                }, this)}
                            </div>
                            </>
                        ), this)
                    }
                    </table>   
                </div>
            </body>
        );
    }
}

export default withRouter(ShowReviews); 